/**
 * Flatten the response data to make it easy to render
 * @param response Synapse response
 * @returns flatten response
 */
const flattenResponse = (response) => {
    const { result, search, header, action } = response;
    const suggestions = [];
    const meta = {
        url: search,
        keyword: header.request.query
    };
    if (result && result.length) {
        result.forEach(row => {
            const rowAsDefault = row.default === true;
            const rowHits = row.hits;
            if (rowHits && rowHits.length) {
                rowHits.forEach((rowHit, hitIdx) => {
                    suggestions.push({
                        label: rowHit.cmd,
                        value: rowHit,
                        highlighted: rowAsDefault && hitIdx === 0,
                        meta
                    });
                    const relations = rowHit.relations;
                    if (relations) {
                        Object.keys(relations).forEach(key => {
                            const relation = relations[key];
                            const relationAsDefault = relation.default === true;
                            const relationHits = relation.hits;
                            if (relationHits && relationHits.length) {
                                relationHits.forEach((relationHit, relationHitIdx) => {
                                    suggestions.push({
                                        label: relationHit.cmd,
                                        value: relationHit,
                                        parent: rowHit,
                                        asSub: true,
                                        highlighted: relationAsDefault && relationHitIdx === 0,
                                        meta
                                    });
                                });
                            }
                        });
                    }
                });
            }
        });
    }
    return {
        header,
        meta,
        suggestions,
        action,
        searchUrl: search
    };
};
/**
 * Validate server response
 * @param response The response to check
 * @returns  valid True if valid
 * @private
 */
const validateResponse = (response) => {
    return !!(response && response.result && Array.isArray(response.result));
};
/**
 * A helper method to parse the query object to query string
 * @param query A query object
 * @returns queryString
 */
const queryToSearchString = (query) => {
    return Object.keys(query).reduce((search, key) => {
        const value = query[key];
        if (typeof value === 'string' || typeof value === 'number' || typeof value === 'boolean') {
            search.push(`${key}=${encodeURIComponent(value)}`);
        }
        else if (value instanceof Array) {
            value.forEach((v) => search.push(`${key}=${encodeURIComponent(v)}`));
        }
        return search;
    }, []).join('&');
};
/**
 * A default transformer. Does nothing
 * @param data Flatten response object
 * @returns data Transformed object
 */
const defaultTransformer = (data) => data;
export { flattenResponse, defaultTransformer, validateResponse, queryToSearchString };
