import { BasicElement, customElement, html } from '@elf/core/lib/elf.js';
import '@elf/coral-button/lib/coral-button.js';
import '@elf/coral-item/lib/coral-item.js';
import '@elf/coral-dialog/lib/coral-dialog.js';
import '@elf/coral-search-field/lib/coral-search-field.js';

/** Insert multiple items to the given array. This is an in-place modification.
* @public
* @function
* @param {Array} ary
* @param {number} at
* @param {Array} items
* @return {Array} In-place edited array
* @example
* insertItems(["a", "b"], 1, ["c", "d"]); // ["a", "c", "d", "b"];
*/
const insertItems = function (ary, at, items) {
	ary.splice.apply(ary, [at, 0].concat(items));
	return ary;
};
/** Returns negative value if a < b, 0 if a === b, and positive value if a > b
* @public
* @function
* @param {number} a
* @param {number} b
* @returns {number}
*/
const numberComparer = function (a, b) {
	return a - b;
};
/**
* Checks, if the given numbers have continuous number series
* @public
* @function
* @param {Array.<number>} sortedIndexes
* @return {boolean}
*/
const isContinuous = function(sortedIndexes) {
	var len = sortedIndexes ? sortedIndexes.length : 0;
	var val = len ? sortedIndexes[0] : 0;
	for(var i = 1; i < len; ++i) {
		var nextVal = sortedIndexes[i];
		if(val + i !== nextVal) {
			return false;
		}
	}
	return len > 0;
};
/** Checks if the given object has "selected" property
* @public
* @function
* @param {Object} item
* @return {boolean}
*/
const isSelectedItem = function(item) {
	return (item && item.selected) ? true : false;
};
/** Checks if the given object is disabled
* @public
* @function
* @param {Object} item
* @return {boolean}
*/
const isEnabledItem = function (item) {
	return (item && !item.disabled) ? true : false;
};
/** Checks if the given object is unmovable
* @public
* @function
* @param {Object} item
* @return {boolean}
*/
const isUnmovableItem = function (item) {
	if(item) {
		return !item.selected || item.disabled ? true : false;
	}
	return false;
};

/** search item label contain specific text
* @public
* @function
* @param {Object} item
* @return {boolean}
*/
const searchItem = function (item) {
	let lowerCasedVal = this.value.toLowerCase();
	return item.label.toLowerCase().includes(lowerCasedVal);
};

/** @public
* @function
* @param {Object} item
*/
const selectItem = function(item) {
	if(item) {
		item.selected = true;
	}
};
/** @public
* @function
* @param {Object} item
*/
const deselectItem = function(item) {
	if(item) {
		item.selected = false;
	}
};
/**
* Returns sorted and selected item indexes from the given list
* @public
* @function
* @param {Array} list
* @return {!Array.<number>}
*/
const getSelectedItemIndexes = function(list) {
	var listCount = list ? list.length : null;
	var indexes = [];
	for(var i = 0; i < listCount; ++i) {
		if(isSelectedItem(list[i])) {
			indexes.push(i);
		}
	}
	return indexes;
};

/** @public
* @function
* @param {Element} elem
* @return {number}
*/
const getElementIndex = function(elem) {
	if(elem) {
		var p = elem.parentNode;
		if(p) {
			var chdr = p.children;
			var childCount = chdr ? chdr.length : 0;
			for(var i = 0; i < childCount; ++i) {
				if(elem === p.children[i]) {
					return i;
				}
			}
		}
	}
	return -1;
};
/**
* Sets scroll to focus on elements
* @public
* @function
* @param {Element} container
* @param {number} itemIndex
*/
const scrollToItem = function(container, itemIndex) {
	if(!container) {
		return;
	}

	var viewHeight = container.clientHeight;
	var contentHeight = container.scrollHeight;

	if(contentHeight <= viewHeight) {
		return; // No scrollbar
	}

	var topView = container.scrollTop;
	var bottomView = topView + viewHeight;

	var chdr = container.children;
	var childCount = chdr.length;
	if(!itemIndex) {
		itemIndex = 0;
	} else if(itemIndex < 0 || itemIndex >= childCount) {
		itemIndex = childCount - 1; // Last item
	}

	var targetItem = chdr[itemIndex];
	var itemHeight = targetItem.offsetHeight;
	// Calculate relative position from item to its parent
	var topPos = itemIndex * itemHeight; // WARNING: This only works when we have constant height
	var bottomPos = topPos + itemHeight;
	if(topPos >= topView && bottomPos <= bottomView) {
		return; // The item is already in the view
	}

	var extraSpace = itemHeight * 2; // Add extra space over the target position to make better UX.
	var targetPos = 0;
	if(topPos < topView) { // Scroll up
		targetPos = topPos - extraSpace;
		if(targetPos < 0) {
			targetPos = 0;
		}
	} else { // Scroll down
		var maxPos = contentHeight - viewHeight;
		targetPos = topView + (bottomPos - bottomView) + extraSpace - itemHeight;
		if(targetPos > maxPos) {
			targetPos = maxPos;
		}
	}

	if(topView !== targetPos) {
		container.scrollTop = targetPos;
	}
};

/** Coral item requires label and value. So we need to convert column object to coral item
* @private
* @param {Object} colObj
* @param {number} idx
* @return {Object}
*/
const _toCoralItem = function(colObj) {
	return {
		label: colObj.label || colObj.title || colObj.name || colObj.field,
		value: colObj.index + '',
		selected: false,
		disabled: colObj.disabled == true
	};
};
/** Valid item must have an id or a field
* @private
* @param {Object} item
* @return {boolean}
*/
const _validateItem = function(item) {
	if(item) {
		if(!item.field) {
			item.field = item.label || item.value || item.id;
		}
		if(item.field) {
			if(!item.title) {
				item.title = item.label || item.field;
			}
			if(!item.id) {
				item.id = item.title;
			}
			return true;
		}
	}
	return false;
};
/**
* @private
* @param {Object} item
* @return {boolean}
*/
const _outEmptyItem = function(item) {
	return item ? true : false;
};
/** Filter out any item with duplicate id
* @private
* @param {Array} ary
* @param {Object} idMap
* @return {Array} Return new array in case of any duplication, otherwise return the same input array
*/
const _screenDuplication = function(ary, idMap) {
	var itemCount = ary ? ary.length : 0;
	var duplication = false;
	for(var i = 0; i < itemCount; ++i) {
		var item = ary[i];
		var itemId = item.id || "";
		if(!itemId || idMap[itemId]) {
			ary[i] = null; // Prevent duplication
			duplication = true;
		} else {
			idMap[itemId] = item;
		}
	}
	if(duplication) {
		ary = ary.filter(_outEmptyItem);
	}
	return ary;
};


export class ColumnSelectionDialog extends BasicElement {

	static get properties() {
		return {
			config: {type: Object},
			data: {type: Object},
			visibleItems: {type: Array},
			isShown: { type: Boolean }
		};
	}

	constructor() {
		super();
		this.isShown = false;
		this._noResult = false;
		this._pendingScroll = false;
		this._highlightedElem = null; // There can only one highlighted elem

		this._initialColumnList = [];
		this._availableColumnList = []; // For keeping original list
		this._filteredColumnList = []; // For filtered list
		this._visibleColumnList = [];
	}

	update(changedProperties) {
		if(changedProperties.has("config")) {
			if(!changedProperties.has("data")) {
				if(Array.isArray(this.config)) {
					this.data = this.config;
					this.config = null;
				} else if(this.config && this.config.data != null) {
					this.data = this.config.data;
				}
			}
			if(!changedProperties.has("visibleItems")) {
				var visibleItems = this.config ? this.config.activeItems || this.config.visibleItems : null;
				if(visibleItems) {
					this.visibleItems = visibleItems;
				}
			}

			if (this.config && this.config.confirm) {
				this.addEventListener('confirm', this.config.confirm);
			}
		}

		if(changedProperties.has("config") ||
			changedProperties.has("data") ||
			changedProperties.has("visibleItems")
		) {
			this._initData();
		}

		super.update(changedProperties);
	}

	render() {
		return html`
			<coral-dialog header="Column Selection" id="columnDialog">
				<div class="column-dialog dialog-theme-wrapper">
					<div class="available-columns">
						<div>Available Columns</div>
						<coral-search-field placeholder="Search" id="searchInput"></coral-search-field>
						<div id="availableColumns" class="section-panel" managed>
							${this._filteredColumnList.map(item => html`<coral-item value="${item.value}" label="${item.label}" ?selected="${item.selected}" class="${item.disabled ? 'disabled' : ''}"/>` )}
							${this._noResult ? html`<coral-item class="no-results" label="No results found"></coral-item>` : html``}
						</div>
					</div>
					<div class="visibility-change-panel">
						<coral-button icon="left" id="removeFromVisibleColumns"></coral-button>
						<coral-button icon="right" id="addToVisibleColumns" ></coral-button>
					</div>
					<div class="visible-columns">
						<div>Visible Columns</div>
						<div id="visibleColumns" class="section-panel" managed>
							${this._visibleColumnList.map(item => html`<coral-item value="${item.value}" label="${item.label}" ?selected="${item.selected}" class="${item.disabled ? 'disabled' : ''}"/> ` )}
						</div>
					</div>
					<div class="order-change-panel">
						<coral-button icon="up-all" id="upAllItem"></coral-button>
						<coral-button icon="up" id="upItem"></coral-button>
						<coral-button icon="down" id="downItem"></coral-button>
						<coral-button icon="down-all" id="downAllItem"></coral-button>
					</div>
				</div>
			</coral-dialog>
		`;
	}

	/**
	 * @override
	 */
	firstUpdated(changedProperties) {
		var shadowRoot = this.shadowRoot;
		shadowRoot.getElementById('addToVisibleColumns').addEventListener('click', this._addToVisibleColumnsClick.bind(this));
		shadowRoot.getElementById('removeFromVisibleColumns').addEventListener('click', this._removeFromVisibleColumnsClick.bind(this));

		shadowRoot.getElementById('searchInput').addEventListener('keyup', this._onInput.bind(this));
		shadowRoot.getElementById('columnDialog').addEventListener('confirm', this._onConfirm.bind(this));
		shadowRoot.getElementById('columnDialog').addEventListener('cancel', this.hide.bind(this));

		// Events for Items position changing
		shadowRoot.getElementById('upItem').addEventListener('click', this._onUpItem.bind(this));
		shadowRoot.getElementById('downItem').addEventListener('click', this._onDownItem.bind(this));
		shadowRoot.getElementById('downAllItem').addEventListener('click', this._onDownAllItem.bind(this));
		shadowRoot.getElementById('upAllItem').addEventListener('click', this._onUpAllItem.bind(this));

		// Events for menu Items management
		var visibleListElem = this.shadowRoot.getElementById('visibleColumns');
		visibleListElem.addEventListener('keydown', this._handleKeydown.bind(this));
		visibleListElem.addEventListener('mouseover', this._handleMouseover.bind(this));
		visibleListElem.addEventListener('mouseout', this._handleMouseout.bind(this));
		visibleListElem.addEventListener('click', this._handleClick.bind(this));
		visibleListElem.addEventListener('dblclick', this._removeFromVisibleColumnsClick.bind(this));

		var availableListElem = this.shadowRoot.getElementById('availableColumns');
		availableListElem.addEventListener('keydown', this._handleKeydown.bind(this));
		availableListElem.addEventListener('mouseover', this._handleMouseover.bind(this));
		availableListElem.addEventListener('mouseout', this._handleMouseout.bind(this));
		availableListElem.addEventListener('click', this._handleClick.bind(this));
		availableListElem.addEventListener('dblclick', this._addToVisibleColumnsClick.bind(this));
	}

	/**
	 * @override
	 */
	updated(changedProperties) {
		if (changedProperties.has('isShown')) {
			this.shadowRoot.getElementById('columnDialog').opened = this.isShown;
			if (!this.isShown) {
				this.remove();
			}
		}

		if(this._pendingScroll) {
			var info = this._getSelectedContainer();
			if(info) {
				scrollToItem(info.container, info.indexes[0]);
			}
			this._pendingScroll = false;
		}
	}

	/**
	 * @public
	 * @param {Object=} options
	 */
	init(options) {
		this.config = options;
	}

	/** Transform and prepare user data for actual usage
	* @private
	*/
	_initData() {
		// Retrieve user options
		var allItems = this.data;
		var visibleItems = this.visibleItems;
		var availableItems = this.config ? this.config.availableItems : null;

		// Ensure that all user given items are valid
		if(Array.isArray(allItems)) {
			allItems = allItems.filter(_validateItem);
		} else {
			allItems = [];
		}
		if(Array.isArray(availableItems)) {
			availableItems = availableItems.filter(_validateItem);
		} else {
			availableItems = [];
		}
		if(Array.isArray(visibleItems)) {
			visibleItems = visibleItems.filter(_validateItem);
		} else {
			visibleItems = [];
		}

		// Remove any duplicate entry by checking duplicate id
		var idMap = {};
		visibleItems = _screenDuplication(visibleItems, idMap);
		availableItems = _screenDuplication(availableItems, idMap);

		var i;
		var itemCount = allItems.length;
		for(i = 0; i < itemCount; ++i) {
			var item = allItems[i];
			if(!idMap[item.id]) {
				idMap[item.id] = item;
				if(allItems[i].visible) {
					visibleItems.push(item);
				} else {
					availableItems.push(item);
				}
			}
		}
		// Combine availableItems and visibleItems to have an allItems list
		allItems = [];
		i = 0;
		for(var key in idMap) {
			item = idMap[key];
			item.index = i++;
			allItems.push(item);
		}

		this._initialColumnList = allItems;
		this._filteredColumnList = this._availableColumnList = availableItems.map(_toCoralItem);
		this._visibleColumnList = visibleItems.map(_toCoralItem);
	}

	/**
	 * Show the dialog
	 * @public
	 */
	show() {
		if (!this.parentNode) {
			document.body.appendChild(this);
		}
		this.isShown = true; // TODO: This is not a correct way of using isShown property
	}

	/**
	 * Hide the dialog
	 * @public
	 */
	hide() {
		var searchInput = this.shadowRoot.getElementById('searchInput');
		if(searchInput) {
			searchInput.value = '';
		}
		this.isShown = false; // TODO: This is not a correct way of using isShown property
	}

	/**
	 * @private
	 * @param {Element} elem
	 * @return {boolean}
	 */
	_isVisibleColumnList(elem) {
		if(elem) {
			return elem.id === 'visibleColumns';
		}
		return false;
	}
	/**
	 * @private
	 * @return {Object}
	 */
	_getSelectedContainer() {
		var selectedIndexes = getSelectedItemIndexes(this._visibleColumnList);
		if(selectedIndexes.length) {
			return {
				container: this.shadowRoot.getElementById('visibleColumns'),
				indexes: selectedIndexes
			};
		}
		selectedIndexes = getSelectedItemIndexes(this._filteredColumnList);
		if(selectedIndexes.length) {
			return {
				container: this.shadowRoot.getElementById('availableColumns'),
				indexes: selectedIndexes
			};
		}
		return null;
	}


	/** This will also remove focused state set by coral-item
	 * @private
	 */
	_unhighlightItems() {
		if(this._highlightedElem) { // There can be only one highlighted Item
			this._highlightedElem.highlighted = false;
			this._highlightedElem.removeAttribute('focused'); // WORKAROUND: Focused and highlighted have the same styles in Halo themes
			this._highlightedElem = null;
		}
	}

	/** @private
	 * @param {number} step if positive highlight will move up, if negative highlight will move down
	 */
	_moveHighlightedItems(step) {
		if (!step) {
			return;
		}

		if(!this._highlightedElem) {
			var info = this._getSelectedContainer();
			if(!info) {
				return;
			}

			this._highlightedElem = info.container.children[info.indexes[0]];
			this._highlightedElem.highlighted = true;
		}

		var pn = this._highlightedElem.parentNode;
		if(!pn) {
			return;
		}

		var chdr = pn.children;
		var childCount = chdr.length;
		var i; // highlighted index
		for(i = 0; i < childCount; ++i) {
			if(chdr[i] === this._highlightedElem) {
				break;
			}
		}
		if(i >= childCount) {
			return;
		}

		i += step;
		if (i >= 0 && i < childCount) {
			this._unhighlightItems();
			this._highlightedElem = chdr[i];
			this._highlightedElem.highlighted = true;
			scrollToItem(pn, i);
		}
	}

	/**
	 * @private
	 * @param {array} fromIndexes
	 * @param {string} movementType
	 */
	_moveVisibleColumnsElement(fromIndexes, movementType) {
		const moveCount = fromIndexes ? fromIndexes.length : 0;
		if(!moveCount) {
			return;
		}
		const list = this._visibleColumnList;
		const listCount = list ? list.length : 0;
		if(!listCount) {
			return;
		}

		const sortedIndexes = fromIndexes.sort(numberComparer);
		const isGrouped = isContinuous(sortedIndexes);
		const isDown = movementType.indexOf('Down') >= 0;
		const minIndex = sortedIndexes[0];
		const maxIndex = sortedIndexes[moveCount - 1];

		let targetIndex = isDown ? maxIndex + 1 : minIndex;

		if(isGrouped) {
			if(isDown) {
				if(movementType === 'Down') {
					targetIndex = targetIndex < listCount ? targetIndex + 1 : listCount;
				} else {
					targetIndex = listCount;
				}
			} else {
				if(movementType === 'Up') {
					targetIndex = targetIndex > 0 ? targetIndex - 1 : 0;
				} else {
					targetIndex = 0;
				}
			}
		}
		scrollToItem(
			this.shadowRoot.getElementById('visibleColumns'),
			targetIndex
		);

		// Collect selected itmes
		const items = new Array(moveCount);
		for(var i = 0; i < moveCount; ++i) {
			let idx = sortedIndexes[i];
			let item = list[idx];
			items[i] = item;
			list[idx] = null;
			item.selected = true; // Ensure that selected item has correct state
		}
		// Insert items back
		insertItems(list, targetIndex, items);

		this._visibleColumnList = list.filter(Boolean);
		this.requestUpdate();
	}

	_onUpItem() {
		this._moveVisibleColumnsElement(getSelectedItemIndexes(this._visibleColumnList), 'Up');
	}

	_onDownItem() {
		this._moveVisibleColumnsElement(getSelectedItemIndexes(this._visibleColumnList), 'Down');
	}

	_onDownAllItem() {
		this._moveVisibleColumnsElement(getSelectedItemIndexes(this._visibleColumnList), 'DownAll');
	}

	_onUpAllItem() {
		this._moveVisibleColumnsElement(getSelectedItemIndexes(this._visibleColumnList), 'UpAll');
	}

	/**
	 * Set filtering result to "searchResult" element
	 * @private
	 * @param {object} e - event object
	 */
	_onInput(e) {
		scrollToItem(this.shadowRoot.getElementById('availableColumns'), 0);
		this._updateSearchResult(e.target.value);
	}

	/**
	 * @private
	 * @param {string} value - search query
	 */
	_updateSearchResult(value) {
		this._noResult = false;
		if(!value) {
			this._filteredColumnList = this._availableColumnList;
		} else {
			this._filteredColumnList = this._availableColumnList.filter(searchItem.bind({value: value}));
			this._noResult = !this._filteredColumnList.length;
		}

		this.requestUpdate();
	}

	/**
	 * @private
	 * @param {Object} e
	 */
	_handleKeydown(e) {
		var handled = false;
		switch (e.keyCode) {
		case 40: // down
		case 9: // tab
			this._moveHighlightedItems(1);
			handled = true;
			break;
		case 38: // up
			this._moveHighlightedItems(-1);
			handled = true;
			break;
		case 13: // enter
			if(this._highlightedElem) {
				this._highlightedElem.click();
				handled = true;
			}
			break;
		}

		if(handled) {
			e.preventDefault();
			e.stopPropagation();
		}
	}

	/**
	 * @private
	 * @param {Object} e
	 */
	_handleMouseover(e) {
		var targetElem = e.target;
		if (targetElem.highlightable) {
			if (this._highlightedElem) {
				if(this._highlightedElem === targetElem) {
					return;
				}
				this._highlightedElem.highlighted = false;
			}
			this._highlightedElem = targetElem;
			this._highlightedElem.highlighted = true;
		}
	}

	/**
	 * @private
	 * @param {Object} e
	 */
	_handleMouseout(e) {
		var targetElem = e.target;
		if (targetElem === this._highlightedElem) {
			targetElem.highlighted = false;
			this._highlightedElem = null;
		}
	}

	/**
	 * @private
	 * @param {object} e - event object
	 */
	_handleClick(e) {
		var selectedElement = e.target; // Target should be coral-item
		if (!selectedElement.highlightable) {
			return;
		}

		var curList = null;
		if(this._isVisibleColumnList(selectedElement.parentNode)) { // When select on one list, deselect everything on another list
			curList = this._visibleColumnList;
			this._availableColumnList.forEach(deselectItem); // Everything in availableColumnListRender will also reset
		} else {
			curList = this._filteredColumnList; // Filtered list
			this._visibleColumnList.forEach(deselectItem);
		}

		var selectedIndex = getElementIndex(selectedElement);

		if(e.metaKey || e.ctrlKey) {
			var curSelectedIndexes = getSelectedItemIndexes(curList);
			var repeatClickAt = curSelectedIndexes.indexOf(selectedIndex);
			if(repeatClickAt >= 0) {
				deselectItem(curList[selectedIndex]);
			} else {
				selectItem(curList[selectedIndex]);
			}
		} else {
			curList.forEach(deselectItem);
			selectItem(curList[selectedIndex]);
		}

		this.requestUpdate();
	}

	/**
	 * Moves selected data to Visible Columns
	 * @private
	 */
	_removeFromVisibleColumnsClick() {
		let selectedItems = this._visibleColumnList.filter(isSelectedItem);
		let movableItems = selectedItems.filter(isEnabledItem);

		if(!movableItems.length) {
			return;
		}

		// Deselect any item that is disabled
		if(movableItems.length < selectedItems.length) {
			selectedItems.forEach(deselectItem);
			movableItems.forEach(selectItem);
		}

		// Move item from visibleList to availableList
		this._availableColumnList = this._availableColumnList.concat(movableItems);
		this._visibleColumnList = this._visibleColumnList.filter(isUnmovableItem);

		this._updateSearchResult(this.shadowRoot.getElementById('searchInput').value);
		this._unhighlightItems();

		// This is because the new ui has not been populated
		this._pendingScroll = true;
		// this.requestUpdate(); Already called by _updateSearchResult
	}

	/**
	 * Moves selected data to Available Columns
	 * @private
	 */
	_addToVisibleColumnsClick() {
		// Note that only visible items (this._filteredColumnList) should be moved
		let selectedItems = this._filteredColumnList.filter(isSelectedItem);
		let movableItems = selectedItems.filter(isEnabledItem);

		if(!movableItems.length) {
			return;
		}

		// Deselect any item that is disabled
		if(movableItems.length < selectedItems.length) {
			selectedItems.forEach(deselectItem);
			movableItems.forEach(selectItem);
		}
		// Deselect any item that is not in the filtered list
		if(this._availableColumnList !== this._filteredColumnList) {
			this._availableColumnList.forEach(deselectItem);
			movableItems.forEach(selectItem);
		}

		// Move item from availableList to visibleList
		this._visibleColumnList = this._visibleColumnList.concat(movableItems);
		this._availableColumnList = this._availableColumnList.filter(isUnmovableItem);

		this._updateSearchResult(this.shadowRoot.getElementById('searchInput').value);

		this._unhighlightItems();

		// This is because the new ui has not been populated
		this._pendingScroll = true;
		// this.requestUpdate(); Already called by _updateSearchResult
	}

	/**
	 * Dispatch Event when user confirm changes
	 * @private
	 */
	_onConfirm() {
		var visibleItems = this._visibleColumnList.map(item => {
			return this._initialColumnList[item.value];
		}); // Return only visible items
		const event = new CustomEvent('confirm', {
			cancelable: true,
			detail: {
				data: visibleItems, // deprecated
				value: visibleItems // Standard
			}
		});
		this.dispatchEvent(event);
		this.hide();
	}
}

customElement('column-selection-dialog')(ColumnSelectionDialog);
