import { Notice } from './Notice';
/**
 * **Warning Notice**\
 * Used to show warning notices in the console.
 */
export class WarningNotice extends Notice {
    constructor() {
        super(...arguments);
        this.type = 'Warning';
    }
    show() {
        /* eslint-disable-next-line no-console */
        console.warn(this.message);
        this.shown = true;
    }
}
