/**
 * Return defined text if the target node content is overflowing
 * @param parentElement Parent element of target
 * @param target Target to check against
 * @param text Text to render
 * @returns Tooltip renderer condition
 */
export const defaultTooltip = (parentElement, target, text) => {
    /**
     * check if target node is inside the rootElement
     * * need this because selector of coral-tooltip is not scoped
     */
    if (!parentElement || !parentElement.shadowRoot || !parentElement.shadowRoot.contains(target) || !text) {
        return;
    }
    // check if content of node is overflowing
    // ! IE has a bug with scrollWidth that it is off by 1px compared to the offsetWidth.
    if (target.scrollWidth > target.offsetWidth && target.getBoundingClientRect().width === target.offsetWidth) {
        return text;
    }
};
